// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
// import vue-function-api
import { plugin as VueFunctionApiPlugin } from 'vue-function-api'

import App from './App.vue'
// import Landing from './Landing'
import router from './router'
import './assets/docs.scss'
// 
import 'vue-awesome/icons'
/*  */
// import Icon from 'vue-awesome/components/Icon'
import * as moment from 'moment'
import iView from 'iview'
import 'iview/dist/styles/iview.css'
/*  */
import 'animate.css'

// vue-function-api
Vue.use(VueFunctionApiPlugin)
// VueprototpyeVue.prototype.xxx = xxx tsdeclare
// main.tswindow(window as any)
// vue
declare module 'vue/types/vue' {
  interface Vue {
    $moment: Function
  }
}

Vue.prototype.$moment = moment
Vue.use(iView)

//  `main .js` 
// Vue.component('awesome-icon', Icon)
Vue.config.productionTip = false
/* tslint-disable no-new */
;(window as any).vm = new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



